@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@300&500&display=swap');

.endzone-button {
    font-family: 'Epilogue', sans-serif;
    font-weight: 600;
    color: white;
    margin: 2vh;
    font-size: 2vh;
    width: auto;
    padding: 1vh;
    padding-left: 3vh;
    padding-right: 3vh;
    height: 5vh;
    text-align: center;
    border-radius: 1vh;
    border: .1vh solid #222a37;
    background-color: #0984e3
}

.endzone-button:hover {
    transform: scale(1.15); 
    transition: ease-in-out;
    animation: hover_pulse 2s infinite;
    cursor: pointer;
    background-color: #0984e3;
}

@keyframes hover_pulse {
	0% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, .75);

	}

    33% {
		box-shadow: 0 0 0 .5vh rgba(9, 132, 227, .5);
	}

	66% {
		box-shadow: 0 0 0 1vh rgba(9, 132, 227, .25);
	}

	100% {
		box-shadow: 0 0 0 0 rgba(9, 132, 227, 0);
	}
}