.terms-div {
    margin-top: 2.5%;
    width: 80%;
    height: 100%;
    padding: 2.5%;
}

.terms {
    width: 99.75%;
    border: .25vh solid #040507;
    border-top: none;
    background-color: white;
    height: 70vh;
    overflow-y: scroll;
}

.terms-text {
    padding: 2.5%;
}

.terms-header {
    text-align: center;
    height: auto;
    background-color: #252830;
    border-top-left-radius: 1vh;
    border-top-right-radius: 1vh;
    border: .25vh solid #040507;
}

.terms-h3 {
    color: white;
    margin-bottom: 1vh;
}

.terms-decision {
    display: inline-flex;
    justify-content: space-evenly;
    border-bottom-left-radius: 1vh;
    border-bottom-right-radius: 1vh;
    background-color: #252830;
    border: .25vh solid #040507;
    border-top: none;
    width: 99.75%;
    height: 10vh;
}